/*! Shared basic style sheet */
html,
body {
  overflow-x: hidden;
  /* 适配移动设备  */
  font-size: 12px;
  /* 保证字体安全，盒子模型统一  */
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, Arial, "Segoe UI", "Microsoft Yahei", "Roboto", "Hiragino Sans GB", "Heiti SC", "WenQuanYi Micro Hei", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  /* 统一行高  */
  line-height: 1.5;
  /* 页面占满  */
  width: 100%;
  /* relative 才能让  absolute 子元素的百分比宽度生效  */
  position: relative;
  /* 字颜色  */
  color: black;
  /* 背景色  */
  background-color: white; }
  @media screen and (min-width: 768px) {
    html,
    body {
      font-size: 14px; } }

* {
  /* 盒子模型统一
        border-box : 定义的元素 height，width 包含 content，padding，border 在内;
        content-box ： 定义的元素 height，width 仅包含 content，不包含 padding，border 在内。
        */
  box-sizing: border-box;
  /* 去除边距 */
  padding: 0;
  margin: 0; }

:focus {
  outline: none; }

/* 滚动条样式 */
::-webkit-scrollbar {
  /* 整体 */
  width: 8px;
  height: 8px;
  background-color: #ccc;
  border-radius: 4px; }

/* ::-webkit-scrollbar:hover {
	width: 16px;
	height: 16px;
	border-radius: 8px;
} */
::-webkit-scrollbar-button {
  /* 上下按钮 */
  background-color: auto; }

::-webkit-scrollbar-track {
  /* 外层轨道 */
  background-color: transparent; }

::-webkit-scrollbar-track-piece {
  /* 内层轨道 */
  background-color: transparent; }

::-webkit-scrollbar-thumb {
  /* 拖动部分 */
  background-color: black;
  border-radius: 4px; }

/* ::-webkit-scrollbar-thumb:hover {
	border-radius: 8px;
} */
::-webkit-scrollbar-corner {
  /* 边角 */
  background-color: auto; }

::-webkit-resizer {
  /* 缩放 */
  background-color: auto; }

/* 清除浮动 */
.clear-float:before,
.clear-float:after {
  content: "";
  display: table; }

.clear-float:after {
  clear: both; }

.clear-float {
  zoom: 1; }

/* 响应式工具 

    .responsive-container ： 自适应模块的包裹元素，提供一个自适应容器，全页面自适应应先加在 body html 上 

    .responsive-abs : 页面浮动上层元素，width、height 自己设置（例如导航，页头，页尾）

    .responsive-rel ：  页面主要内容元素，width、height 自动占满整个容器，可利用 padding 来将 .responsive-abs 元素覆盖的部分隔开

*/
.responsive-abs {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2; }

.responsive-rel,
.responsive-container {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
  top: 0;
  z-index: 1; }

