/** 
 * Page head
 */
.page-head {
  /* animation */ }
  .page-head > .banner {
    padding: 90px 0; }
  .page-head .author {
    transition: transform 1s ease-in-out 1.5s; }
    @media (min-width: 1200px) {
      .page-head .author {
        transform: translateX(50%); } }
    .page-head .author .name {
      position: relative;
