        transform: scaleX(0);
        transform-origin: 0 0;
        transition: all 1s linear .5s; }
      .page-head .author .name::after {
        transform-origin: 100% 0;
        bottom: 0; }
      .page-head .author .name:hover::before, .page-head .author .name:hover::after {
