/*! Custom document style */

[content="docs-doc"] {
    font-size: 1.2rem;

    h1, h2, h3, h4, h5, h6 {
        color: #2196f3;
        font-weight: bold;
    }

    header {
        padding-bottom: 1rem;

        h1 {
            color: black;
        }
    }

    p {
        line-height: 1.8;
    }

    ol {
        padding-left: 2rem;
        margin-left: 1rem;

        li {
            p:first-child {
                font-weight: 600;
                margin-bottom: .25rem;
            }
        }
    }

    ul {
        padding-left: 2rem;
        margin-left: 1rem;
        
        li {
            list-style-type: square;

            p:first-child {
                font-weight: 600;
                margin-bottom: .25rem;
            }

            li {
                list-style-type: circle;
            }
        }
    }

    blockquote {
        padding: .25rem 0 .25rem .5rem;
        margin-left: 1rem;
        color: #6c757d;
        font-style: italic;
        border-left: .25rem solid #007bff;
        background-color: #f8f9fa;

        > p {
            margin-bottom: .5rem;
            
            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    code {
        display: inline-block;
        padding: 0 .25rem;
        border-radius: .25rem;
        background-color: #f8f9fa;
    }

    pre {
        overflow: auto;
        padding: 1rem 0;
        background-color: #f8f9fa;

        > code {
            display: block;  
            position: relative;
            padding: 0 0 0 3rem;
            border-radius: none;
            background-color: none;

            /* line-number */
            &::before {
                //content: '1\A 2\A 3\A 4\A 5\A 6\A 7\A 8\A 9\A 10\A 11\A 12\A 13\A 14\A 15\A 16\A 17\A 18\A 19\A 20\A 21\A 22\A 23\A 24\A 25\A 26\A 27\A 28\A 29\A 30\A 31\A 32\A 33\A 34\A 35\A 36\A 37\A 38\A 39\A 40\A 41\A 42\A 43\A 44\A 45\A 46\A 47\A 48\A 49\A 50\A 51\A 52\A 53\A 54\A 55\A 56\A 57\A 58\A 59\A 60\A 61\A 62\A 63\A 64\A 65\A 66\A 67\A 68\A 69\A 70\A 71\A 72\A 73\A 74\A 75\A 76\A 77\A 78\A 79\A 80\A 81\A 82\A 83\A 84\A 85\A 86\A 87\A 88\A 89\A 90\A 91\A 92\A 93\A 94\A 95\A 96\A 97\A 98\A 99';
                content: attr(line-number);
                position: absolute;
                left: 0;
                width: 2.5rem;
                padding-right: .5rem;
                color: #bbb;
                text-align: right;
                border-right: 1px solid #ced4da;
            }
        }
    }
}